import React from 'react';
import { Image, Carousel, Container } from 'react-bootstrap';

export default ({ customCarouselData }) => (
  <Carousel touch slide pauseOnHover>
    {
      customCarouselData.map(el => (
        <Carousel.Item>
          <div style={{ backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.6)), url(${el.img})`, position: 'relative' }} className="color-overlay" />
          <Container>
            <Carousel.Caption>
              <div>
                <h3>{el.label}</h3>
                <p>{el.text}</p>
              </div>
            </Carousel.Caption>
          </Container>
        </Carousel.Item>
      ))
    }
  </Carousel>
);
