import {
  Card,
  Col, Container, Row,
} from 'react-bootstrap';
import ModalImage from 'react-modal-image';
import React from 'react';
import ContentLine from './contentLine';

export default ({ heading, photos }) => (
  <div style={{ paddingTop: '40px', paddingBottom: '40px' }}>
    <Container>
      <Row>
        <Col className="text-center">
          <h1>{heading}</h1>
          <ContentLine />
        </Col>
      </Row>
      {
      photos.map(el => (
        <Row>
          {
            el.map(pic => (
              <Col mb={4} md={{ span: 4 }} style={{ paddingBottom: '20px' }}>
                <Card>
                  <ModalImage
                    small={pic.img}
                    large={pic.img}
                    alt={pic.alt}
                  />
                </Card>
              </Col>
            ))
          }
        </Row>
      ))
    }
    </Container>
  </div>
);
