import {
  Col, Image, Row,
} from 'react-bootstrap';
import React, { Fragment } from 'react';
import ShowMoreText from 'react-show-more-text';
import { show } from '../helpers/data';


function isEven(value) {
  return value % 2 === 0;
}

function rightAlign(currentValue) {
  return (
    <Fragment>
      <Col md={{ span: 7, order: 2 }} className="box-shadow">
        <Image
          src={currentValue.image}
          fluid
        />
      </Col>
      <Col md={{ span: 5, order: 1 }}>
        <h2 className="featurette-heading">{currentValue.header}</h2>
        <ShowMoreText
          lines={6}
          more={<span className="text-muted">{show.more}</span>}
          less={<span className="text-muted">{show.less}</span>}
          anchorClass=""
          expanded={false}
        >
          <p className="lead">{currentValue.text}</p>
        </ShowMoreText>
      </Col>
    </Fragment>
  );
}

function leftAlign(currentValue) {
  return (
    <Fragment>
      <Col md={{ span: 7, order: 1 }} className="box-shadow">
        <Image
          src={currentValue.image}
          fluid
        />
      </Col>
      <Col md={{ span: 5, order: 2 }}>
        <h2 className="featurette-heading">{currentValue.header}</h2>
        <ShowMoreText
          lines={6}
          more={<span className="text-muted">{show.more}</span>}
          less={<span className="text-muted">{show.less}</span>}
          anchorClass=""
          expanded={false}
        >
          <p className="lead">{currentValue.text}</p>
        </ShowMoreText>
      </Col>
    </Fragment>
  );
}

export default ({ indexContentData }) => (
  <Fragment>
    {
      indexContentData.map((currentValue, index) => (
        <Row className="featurette">
          {
            isEven(index) ? rightAlign(currentValue) : leftAlign(currentValue)
          }
        </Row>
      ))
    }
  </Fragment>
);
